.cards {
	border-top: 1px solid $card-border-color;
	border-left: 1px solid $card-border-color;
	// font-size: 0
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: stretch; }

.card {
	border-right: 1px solid $card-border-color;
	border-bottom: 1px solid $card-border-color;
	padding: 22px 18px;
	// margin-left: -.36em
	@include hover {
		.card__title {
			color: $main-green;
			transition: 0.5s color; } }
	&__big {
		// display: inline-block
		// width: 25%
		flex: 0 0 25%;
		min-width: 250px;
		@include sm {
			flex: 1 0 50%; } }
	&__image {
		padding-bottom: 275px;
		@include cover;
		position: relative; }
	&__freemark {
		position: absolute;
		bottom: 0;
		right: 0;
		.icon {
			font-size: 3em;
			svg {
				height: 30px;
				width: 30px; } } }
	&__title {
		font-family: Roboto;
		font-size: 22px;
		font-weight: bold;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.09;
		text-align: center;
		color: $card-title-color;
		text-transform: uppercase;
		transition: 0.5s color; }
	&__price {
		font-family: CorpidE1sSCd;
		margin-bottom: 0.5em;
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		color: $card-price-color; }
	&__id {
		font-family: CorpidE1sSCd;
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		margin-bottom: 0.5em;
		&--id {
			color: $id-color-lighter; }
		&--number {
	  	color: $id-color-darker; } }
	&__divider {
		margin: 16px auto; }
	.select2-container--default .select2-selection--single .select2-selection__rendered {
		color: $dark_blue; }
	&__link {
		color: $white;
		border-radius: 4px;
		text-align: center;
		text-transform: uppercase;
		padding: 7px 30px;
		border-radius: 20px;
		background-color: $main-green; } }

.small-card {
	display: block;
	min-width: 97px;
	font-family: $corp;
	text-align: center;
	&__image {
		@include cover;
		padding-bottom: 80px; }
	&__title {
		margin-top: 14px;
		font-size: 18px;
		font-weight: bold;
		line-height: 1.22;
		letter-spacing: -0.5px;
		color: $dark_blue; }
	&__id {
		font-size: 13px;
		line-height: normal;
		color: $text_black; }
	&__price {
		font-size: 15px;
		font-weight: 900;
		line-height: normal;
		color: $blue; } }

