
.filter {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 30px;
    &__col {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        &:first-child {
            flex-grow: 1;
            min-width: 440px;
            margin-right: auto; } }

    &__found {
        font-family: $corp;
        font-size: 24px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #3a5864;
        margin-right: auto;
        &--bold {
            font-weight: 700; } }
    &__select-wrapper {
        padding: 0 20px;
        font-size: 0;
        margin-bottom: 26px;
        & > * {
            display: inline-block;
            vertical-align: middle; } }
    &__country-flag {
        max-height: 30px;
        max-width: 40px;
        overflow: hidden;
        img {
            max-height: 30px;
            display: inline-block;
            vertical-align: middle; } }
    &__select-globe {
        margin: 0 15px; }
    &__select {
        width: 232px; }
    &__tooltip {
        margin-left: 6px;
        cursor: pointer;
        .icon {
            font-size: 28px; } }
    &__range {
        display: inline-block;
        width: 400px;
 }        // margin-top: 26px

    &__breadcrumb {
        margin-right: auto; } }
